import React, { Component } from "react";
import "./styles.css";
import axios from "axios";

class App extends Component {
  constructor() {
    super();
    this.state = {
      data: []
    };
  }
  componentDidMount() {
    axios.get("https://jsonplaceholder.typicode.com/posts").then((e) => {
      this.setState({
        data: e.data
      });
    });
  }
  render() {
    return (
      <div>
        <ul>
          {this.state.data.map((e) => (
            <li>
              <p>Id {e.id}</p>
              <p>Title: {e.title}</p>
              <p>Body : {e.body}</p>
            </li>
          ))}
        </ul>
      </div>
    );
  }
}

export default App;
